.buyButtonContainer {
  cursor: pointer;
  margin: 0 10.5px 0 10.5px;
  padding: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  width: auto;
  height: 0px;
  left: 490px;
  top: 890px;
  background: var(--color-brand-300);
  visibility: hidden;
  opacity: 0;
  transition-duration: 1s;
}
.productCardContainer:hover .buyButtonContainer {
  visibility: visible;
  opacity: 1;
  height: 42px;
}

.buyButtonContainer button {
  cursor: pointer;
  display: flex;
  border: none;
  width: 86px;
  height: 0px;
  font-family: 'Montserrat';
  background: var(--color-brand-300);
  color: var(--color-brand-50);
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  transition: 1s;
}

.productCardContainer:hover .buyButtonContainer button {
  height: 17px;
}
