.brandGrid {
  margin: auto;
  width: 1000px;
  height: 247px;
  padding: 30px 220px 0 220px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
@media (max-width: 1400px) {
  .brandGrid {
    padding: 30px 0 0 0;
    width: 490px;
    height: 514px;
    flex-direction: column;
    margin: auto;
    justify-content: space-between;
  }
}
@media (max-width: 900px) {
  .brandGrid {
    width: auto;
    height: 391.46px;
  }
}
