.brandCardContainer {
  position: relative;
  width: fit-content;
  height: 247px;
}
@media (max-width: 1400px) {
  .brandCardContainer {
    height: 260px;
  }
}
@media (max-width: 900px) {
  .brandCardContainer {
    height: auto;
  }
  .brandCardContainer img {
    width: 370px;
  }
}

.brandCardText {
  position: absolute;
  width: 131px;
  height: 96px;
  left: 39px;
  top: 30%;

  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  font-size: 26px;
  line-height: 32px;
  letter-spacing: 0.2em;
  text-transform: uppercase;

  color: #ffffff;
}
