.productCardContainer {
  margin: auto;
  padding-bottom: 5%;
  display: flexbox;
  flex-direction: column;
  width: 235px;
  height: 405px;
  background: #ffffff;
  border: 1px solid #eeeeee;
  box-sizing: border-box;
  transition: height 1s, transform 2s;
}
@media (max-width: 1400px) {
  .productCardContainer {
    width: 235px;
  }
}
.productCardContainer:hover {
  height: 450px;
}

.heartContainer {
  position: absolute;
  top: 10px;
  margin-left: 197px;
}

.imgContainer {
  height: 308px;
  display: flex;
  margin: 0 10.5px 0 10.5px;
}

.imgContainer img {
  height: auto;
  display: flexbox;
  margin: auto;
}

.productCardDescription {
  padding-bottom: 10.5px;
}

.productCardContainer p {
  margin: 0 10.5px 0 10.5px;
  width: 196px;
  min-height: 32px;
  height: auto;
  font-family: 'Nunito';
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: var(--color-brand-700);
}

.priceContainer {
  margin: 0 10.5px 0 10.5px;
  font-family: 'Nunito';
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 19px;

  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: var(--color-brand-900);
}
