.productCarousel {
  margin: auto;
  height: 550px;
  display: flex;
  flex-direction: column;
  padding: 57px 220px 0 220px;
  align-items: center;
}
@media (max-width: 1400px) {
  .productCarousel {
    padding: 57px 0 0 0;
  }
}

.productCarousel h2 {
  display: flex;
  width: 172px;
  height: 35px;
  left: 634px;
  top: 422px;

  font-family: 'Nunito';
  font-style: normal;
  font-weight: 400;
  font-size: 26px;
  line-height: 35px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

.slick-slider {
  display: flex;
  margin-top: 20px;
  max-width: 1020px;
  justify-content: space-between;
}
@media (max-width: 1400px) {
  .slick-slider {
    width: 765px;
    height: 514px;
    justify-content: space-between;
    padding: 0;
  }
}
@media (max-width: 900px) {
  .slick-slider {
    width: 390px;
    height: 514px;
    justify-content: space-between;
  }
}
.slick-list {
  width: 1020px;
  height: 470px;
}

.slick-prev,
.slick-next {
  position: absolute;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  height: 53px;
  width: 40px;
  background-color: var(--color-brand-600) !important;
}
@media (max-width: 900px) {
  .slick-prev,
  .slick-next {
    display: none !important;
  }
}

.slick-prev:before {
  opacity: 1 !important;
  content: url(../../files/images/back.png) !important;
}

.slick-prev {
  left: -50px;
}

.slick-next:before {
  opacity: 1 !important;
  content: url(../../files/images/forward.png) !important;
}

.slick-next {
  right: -50px;
}

.slick-disabled {
  opacity: 0.25;
}
