.headerContainer {
  display: flex;
  min-width: 1440px;
  background-color: var(--color-brand-600);
}

.header {
  margin: auto;
  width: 1000px;
  height: 90px;
  padding: 0 220px 0 220px;
  background-color: var(--color-brand-600);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1400px) {
  .headerContainer {
    min-width: 0px;
  }
  .header {
    width: 750px;
    height: 90px;
    padding: 0 20px 0 20px;
  }
}
@media (max-width: 900px) {
  .header {
    width: 414px;
  }
}

.brandNameContainer {
  width: 75px;
  height: 32px;
}

.brand {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 0.625em;
}

.brandName {
  width: 75px;
  height: 32px;
  font-family: 'Montserrat';
  color: var(--color-brand-50);
  font-style: normal;
  font-weight: 900;
  font-size: 26px;
  line-height: 32px;
  letter-spacing: 0.2em;
  text-transform: uppercase;
}

.shoppingIcon {
  height: 32px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cartCounterContainer {
  background-color: var(--color-brand-200);
  display: flexbox;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  position: absolute;
  left: 75%;
  bottom: 50%;
  align-items: center;
  text-align: center;
}

.cartCounter {
  width: 16px;
  height: 16px;
  padding: auto;
  justify-content: center;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  font-size: 10px;
  line-height: 12px;
  text-transform: uppercase;
  color: var(--color-brand-900);
}
